import React, { useState, useEffect, useReducer } from "react"
import SEO from "../components/seo"
import ProjectLayout from "../components/projectLayout"

import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import {
  automachineANames,
  currencyUnits,
} from "../components/republica/constants"

const newspaper = [
  "Republica Independent Reporter",
  "The New Republicar",
  "RNN - Republica News Network",
  "People's National Media Conglomerate of Republica",
]
const machineBasePrice = 10

function ProjectRepublica() {
  const initialState = {
    currDollars: 0,
    totalDollars: 0,
    productionMultiplier: 1,
    numAutoMachineA: 0,
    autoMachineAPrice: machineBasePrice,
  }
  const [state, dispatch] = useReducer(reducer, initialState)
  const [gameStage, setGameStage] = useState(0)

  useEffect(() => {
    setInterval(() => {
      dispatch({ type: "iterateAutoMachineA" })
    }, 1000)
  }, [])

  function reducer(state, action) {
    switch (action.type) {
      case "iterateAutoMachineA":
        return {
          ...state,
          currDollars: state.currDollars + state.numAutoMachineA,
          totalDollars: state.totalDollars + state.numAutoMachineA,
        }
      case "printDollar":
        return {
          ...state,
          currDollars: state.currDollars + 1 * state.productionMultiplier,
          totalDollars: state.totalDollars + 1 * state.productionMultiplier,
        }
      case "purchaseAutoMachineA":
        return {
          ...state,
          currDollars: state.currDollars - state.autoMachineAPrice,
          numAutoMachineA: state.numAutoMachineA + 1,
        }
      case "updateAutoMachineAPrice":
        return {
          ...state,
          autoMachineAPrice: Math.round(
            machineBasePrice * 1.2 ** state.numAutoMachineA
          ),
        }
      default:
        throw new Error()
    }
  }

  //automachine A logic
  function purchaseAutoMachineA() {
    if (state.currDollars >= state.autoMachineAPrice) {
      dispatch({ type: "purchaseAutoMachineA" })
      dispatch({ type: "updateAutoMachineAPrice" })
    } else {
      alert("Insufficient funds")
    }
  }
  function AutoMachineAComponent() {
    return (
      <>
        <p>
          Number of {automachineANames[gameStage]}: {state.numAutoMachineA}
          <br />${state.autoMachineAPrice} per {automachineANames[gameStage]}
        </p>
        <button
          onClick={() => purchaseAutoMachineA()}
          disabled={state.currDollars < state.autoMachineAPrice}
        >
          Purchase {automachineANames[0]}
        </button>
      </>
    )
  }

  return (
    <ProjectLayout>
      <SEO title="Republica Game" />
      <Row>
        <h2>
          National coffers: {state.currDollars.toLocaleString()}{" "}
          {currencyUnits[gameStage]}
        </h2>
        <Col sm={2}>
          <button onClick={() => dispatch({ type: "printDollar" })}>
            Print {currencyUnits[gameStage]}
          </button>
        </Col>
        <hr />
      </Row>
      <Row>
        <Col sm={8}>
          <h4>Industry</h4>
          {state.totalDollars > 10 && <AutoMachineAComponent />}
        </Col>
        <Col sm={4}>
          <h4 id="newstitle">{newspaper[gameStage]}</h4>
          <div
            id="newsbox"
            style={{
              overflow: "scroll",
              "overflow-x": "hidden",
              "overflow-y": "auto",
              height: "300px",
              textAlign: "justify",
            }}
          >
            <p>
              {">"} Fearless leader founds new nation of Republica after daring
              escape into unprotected waters outside of Unified Nations
              jurisdiction.
              <br />
              Republica will use {"\u03B1RD"} (Alpha Republica Dollar) as its
              main unit of currency.
              <br />
              <strong>Long live Republica!</strong>
            </p>
          </div>
        </Col>
      </Row>
      <button onClick={() => dispatch({ type: "iterateAutoMachineA" })}>
        Iterate Automachine A
      </button>
      <button onClick={() => console.log(state)}>State</button>
    </ProjectLayout>
  )
}

export default ProjectRepublica
